<script>
    import AppBarPage from '@/components/AppBar.vue'
    import TableComponents from '@/packages/Table.vue'
    import SelectComponents from '@/packages/Select.vue'

    import {httpHandle} from '@/http/http'  // api
    import { showSuccessToast,showFailToast,showToast } from 'vant'
    import { showDialog  } from 'vant'

  export default {
    components:{
        AppBarPage,
        SelectComponents,
        TableComponents
    },
    data: () => ({
        bufferRow:{},  // 行数据
        dataList:[],   // 列表数据

        type:"",   // 不良类别
        typeSelectOption:[],   // 不良类别 数据  


        department:"",   // 下发到处理部门
        departmentSelectOption:[],   // 下发到处理部门 数据  



        switch111:false,   // 质量 是否上升
        switch222:false,   // 工艺 是否上升
        switch333:false,   // 技术 是否上升


        conductor1:"",   // 质量 处理人
        conductorSelectOption1:[],   // 质量 处理人
        conductor2:"",   // 工艺 处理人
        conductorSelectOption2:[],   // 工艺 处理人
        conductor3:"",   // 技术 处理人
        conductorSelectOption3:[],   // 技术 处理人


        opinion1:"",   // 不良描述
        opinion2:"",   // 问题描述
        opinion3:"",   // 整改结果
        opinion4:"",   // 处理措施
        opinion5:"",   // 放行说明

        opinion7:"",   // 处理意见  质量
        opinion8:"",   // 处理意见  技术
        opinion9:"",   // 处理意见  工艺



        showLi1:false,   // 显示 检验信息
        showLi2:false,   // 显示 整改信息
        showLi3:false,   // 显示 质量处理
        showLi4:false,   // 显示 技术处理
        showLi5:false,   // 显示 工艺处理
        showLi6:false,   // 显示 放行信息

        readonlyPage:true,   // 只读


        readonlyPage111:false,   // 只读  检验信息
        readonlyPage222:false,   // 只读  整改信息 
        readonlyPage333:false,   // 只读  质量处理   技术处理  工艺处理



    }),
    watch:{
        switch111(value){
            this.conductor1=""
            this.$nextTick(()=>{
                this.$refs.select777 && this.$refs.select777.reset()
            })
        },
        switch222(value){
            this.conductor2=""
            this.$nextTick(()=>{
                this.$refs.select888 && this.$refs.select888.reset()
            })
        },
        switch333(value){
            this.conductor3=""
            this.$nextTick(()=>{
                this.$refs.select999 && this.$refs.select999.reset()
            })
        }
    },
    created(){
        this.initFunc()

        this.typeHTTP()
        this.initRepairman1()   // 质量 放行人111
        this.initRepairman2()   // 工艺 放行人22
        this.initRepairman3()   // 工艺 放行人33


    },
    methods: {
        // 全局 气泡 提示
        GlobalTooltipFunc(text=""){
            showToast({
                message:text,
                overlay:true,
                closeOnClickOverlay:true,
                duration:0
            })
        },
        // 数据字典 格式化
        FormatDictionary(key="",valueKey=""){
            const _bufferDictionaries=JSON.parse(localStorage.getItem("bufferDictionaries")||"{}")
            const _obj=(_bufferDictionaries[key] || [] )
            const _option=_obj.filter(o=>valueKey==o.value)[0]||{}
            return _option
        },
        // 放行人 质量 111
        async initRepairman1(key=''){
            const {code,data=[]}= await httpHandle({
                url:'/system/user/list',
                method:"get",
                url_params:{
                    userKey:key
                }
            }) 

            if(code==200){
