        // 初始化
        async initFunc(){
            const {ttQmDefectId}=this.$route.query

            const _bufferDictionaries=JSON.parse(localStorage.getItem("bufferDictionaries")||"{}")

            // 处理部门
            const _departmentAttribute=_bufferDictionaries["qm_process_dept"]||[]    // 属性
            this.departmentSelectOption=_departmentAttribute.map(o=>Object.assign({text:o.lable,value:o.value}))   // 属性数据

            
            // 详情 基础数据
            const {code,data={}}= await httpHandle({
               url:`/iiot/qmDefect/${ttQmDefectId}`,
               method: "get",
            })
           
            if(code==200){
                this.bufferRow=data


                // 控制页面
                // data.defectStatus='90'
                // data.issueProcessDept='3'
                switch ( data.defectStatus ) {
                    case '10':   // 检验信息
                        this.showLi1=true
                        this.showLi2=false
                        this.showLi3=false
                        this.showLi4=false
                        this.showLi5=false
                        this.showLi6=false
                        this.readonlyPage=false

                        break;
                    case '20':  // 整改信息
                        this.showLi1=true
                        this.showLi2=true
                        this.showLi3=false
                        this.showLi4=false
                        this.showLi5=false
                        this.showLi6=false
                        this.readonlyPage=false


                        this.readonlyPage111=true  // 只读  检验信息

                        break;
                    case '30':  

                        this.readonlyPage111=true  // 只读  检验信息
                        this.readonlyPage222=true  // 只读  整改信息


                        // 质量处理
                        if(data.issueProcessDept=='1'){
                            this.showLi1=true
                            this.showLi2=true
                            this.showLi3=true
                            this.showLi4=false
                            this.showLi5=false
                            this.showLi6=false
                            this.readonlyPage=false
                        }

                        // 技术处理
                        if(data.issueProcessDept=='2'){
                            this.showLi1=true
                            this.showLi2=true
                            this.showLi3=false
                            this.showLi4=true
                            this.showLi5=false
                            this.showLi6=false
                            this.readonlyPage=false

                        }

                        // 工艺处理
                        if(data.issueProcessDept=='3'){
                            this.showLi1=true
                            this.showLi2=true
                            this.showLi3=false
                            this.showLi4=false
                            this.showLi5=true
                            this.showLi6=false
                            this.readonlyPage=false
                        }


                        break;
                    case '40': // 放行信息
                        this.readonlyPage111=true  // 只读  检验信息
                        this.readonlyPage222=true  // 只读  整改信息
                        this.readonlyPage333=true  // 只读  


                        // 质量处理
                        if(data.issueProcessDept=='1'){
                            this.showLi1=true
                            this.showLi2=true
                            this.showLi3=true
                            this.showLi4=false
                            this.showLi5=false
                            this.showLi6=true
                            this.readonlyPage=false
                        }

                        // 技术处理
                        if(data.issueProcessDept=='2'){
                            this.showLi1=true
                            this.showLi2=true
                            this.showLi3=false
                            this.showLi4=true
                            this.showLi5=false
                            this.showLi6=true
                            this.readonlyPage=false
                        }

                        // 工艺处理
                        if(data.issueProcessDept=='3'){
                            this.showLi1=true
                            this.showLi2=true
                            this.showLi3=false
                            this.showLi4=false
                            this.showLi5=true
                            this.showLi6=true
                            this.readonlyPage=false
                        }


                        break;
                    case '90': // 不可编辑

                        // 质量处理
                        if(data.issueProcessDept=='1'){
                            this.showLi1=true
                            this.showLi2=true
                            this.showLi3=true
                            this.showLi4=false
                            this.showLi5=false
                            this.showLi6=true
                            this.readonlyPage=true
                        }

                        // 技术处理
                        if(data.issueProcessDept=='2'){
                            this.showLi1=true
                            this.showLi2=true
                            this.showLi3=false
                            this.showLi4=true
                            this.showLi5=false
                            this.showLi6=true
                            this.readonlyPage=true
                        }

                        // 工艺处理
                        if(data.issueProcessDept=='3'){
                            this.showLi1=true
                            this.showLi2=true
                            this.showLi3=false
                            this.showLi4=false
                            this.showLi5=true
                            this.showLi6=true
                            this.readonlyPage=true
                        }

                        break;
                    default:
                        break;
                }


                // 初始化
                this.opinion5=data.releaseMeasures||'' // 放行说明
                this.opinion3= data.repairResult||''   // 整改结果
                this.opinion4= data.repairMeasures||''   // 处理措施   
                this.opinion2=data.scrapReason||''  // 问题描述
                // this.type= data.tmBasReasonId   // 不良类别

                this.opinion7=data.qualityEngineerD||''  // 处理意见  质量 
                this.opinion8=data.technologyD||''  // 处理意见     技术
                this.opinion9=data.technologicalD||''  // 处理意见   工艺


                // 是否上升
                this.switch111=data.isUpgrade=='1'?true:false   // 质量 是否上升
                this.switch222=data.isUpgrade=='1'?true:false   // 工艺 是否上升
                this.switch333=data.isUpgrade=='1'?true:false  // 技术 是否上升

                // 放行人


                this.$nextTick(()=>{
                    setTimeout(()=>{
                        this.conductor1=data.upgradeProcessBy   // 质量 处理人
                        this.conductor2=data.upgradeProcessBy   // 工艺 处理人
                        this.conductor3=data.upgradeProcessBy   // 技术 处理人

                        this.$refs.select777 && this.$refs.select777.setValue( data.upgradeProcessBy  ) // 下拉框
                        this.$refs.select888 && this.$refs.select888.setValue( data.upgradeProcessBy  ) // 下拉框
                        this.$refs.select999 && this.$refs.select999.setValue( data.upgradeProcessBy  ) // 下拉框
                    },1000)

                })


                this.department=data.issueProcessDept // 处理部门

                setTimeout(()=>{
                    this.$refs.select99 && this.$refs.select99.setValue( data.issueProcessDept )  // 处理部门
                },1000)
            }

            // 列表 数据
            const _result= await httpHandle({
                url:`/iiot/qmDefectDetail/list`,
                method: "get",
                url_params:{
                    ttQmDefectId: ttQmDefectId
                }
            })

            if(code==200){
                this.dataList= _result.data||[]
            }

        },
        // 不良类别
        async typeHTTP(){
            const {code,data=[]}= await httpHandle({
                url:`/iiot/reason/list`,
                method: "post",
                payload:{
                    // pageNum:1,
                    // pageSize:10,          
                    reasonNo:"",
                    params:{
                        reasonTypeList:['MS','F']
                    }
                }
            })
           
            if(code==200){
                this.typeSelectOption=data.map(o=>Object.assign({text:o.reasonNo+'-'+o.reasonName,value:o.tmBasReasonId}))   // 不良类别
            
                setTimeout(()=>{
                    // console.log(data)
                    this.$refs.select33 && this.$refs.select33.setValue( this.bufferRow.tmBasReasonId )  // 不良类别
                },600);
            }
        },
        // 检验信息提交
        async submitMessage(){
            const {bufferRow}=this

            if(!this.type){
                showFailToast("不良类别必填！")
                return
            }

            if(!this.opinion2.trim()){
                showFailToast("问题描述必填！")
                return
            }

            const _json={
                tmBasReasonId: this.type,   // 不良类别
                scrapReason: this.opinion2,  // 问题描述
                dataList: this.dataList,  // 明细列表reasonDesc
                ttQmDefectId: bufferRow.ttQmDefectId

            }

            const {code,data={}}= await httpHandle({
               url:'/iiot/qmDefect/productDefect',
               method: "post",
               payload:_json
            })

            if(code==200){
                showSuccessToast('提交成功！')

                this.$router.push({
                    path:'/processDisqualification/index', 
                    query:{  }
                }) 
            }

 
        },
        // 整改信息 
        async submitRemould(){
            const {bufferRow}=this

            
            // if(!this.department){
            //     showFailToast("处理部门必填！")
            //     return
            // }

            if(!(this.opinion3.trim())){
                showFailToast("整改结果必填！")
                return
            }

            if(!this.opinion4.trim()){
                showFailToast("处理措施必填！")
                return
            }

            const _json={
                issueProcessDept: this.department,  // 处理部门
                repairResult: this.opinion3,   // 整改结果
                repairMeasures: this.opinion4,   // 处理措施    
                ttQmDefectId: bufferRow.ttQmDefectId

            }

            const {code,data={}}= await httpHandle({
               url:'/iiot/qmDefect/repairDefect',
               method: "post",
               payload:_json
            })

            if(code==200){
                showSuccessToast('提交成功！')

                this.$router.push({
                    path:'/processDisqualification/index', 
                    query:{  }
                }) 
            }

        },
        // 质量处理
        async submitDepartmentQuality(){
            const {bufferRow}=this

            if(!(this.opinion7.trim())){
                showFailToast("处理意见必填！")
                return
            }

            if(this.switch111 && !this.conductor1){
                showFailToast("放行人必选！")
                return
            }


            const _json={
                qualityEngineerD: this.opinion7,  // 处理意见  
                ttQmDefectId: bufferRow.ttQmDefectId,
                isUpgrade: this.switch111?'1':'0',   // 是否上升
                upgradeProcessBy:this.conductor1,   // 放行人
            }

            const {code,data={}}= await httpHandle({
               url:'/iiot/qmDefect/qualityEngineerCommit',
               method: "post",
               payload:_json
            })

            if(code==200){
                showSuccessToast('提交成功！')
                this.$router.push({
                    path:'/processDisqualification/index', 
                    query:{  }
                }) 
            }

        },
