        // 放行说明
        async submitThrough(){
            const {bufferRow}=this


            if(!this.opinion5.trim()){
                showFailToast("放行说明必填！")
                return
            }

            const _json={
                releaseMeasures: this.opinion5, // 放行说明
                ttQmDefectId: bufferRow.ttQmDefectId

